<template>
  <div>
    <h4 class="ms-4">Profile</h4>
    <v-card color="basil" flat >
            <v-form>
              <v-row>
                <v-col cols="12" md="12" class="py-0">
                    <v-avatar  size="100">
                      <img :src="form1.avatar_media_url" alt="John" @click="pickFile('avatar')">
                    </v-avatar>
                    <input type="file" style="display: none" id="avatar" name="avatar" accept="image/*" @change="onFilePicked">
                </v-col>
                <v-col cols="12" md="6" class="py-0">
                  <v-text-field label="Name" required ></v-text-field>
                </v-col>
                <v-col cols="12" md="6" class="py-0">
                  <v-text-field label="E-mail address" required ></v-text-field>
                </v-col>
                <v-col cols="12" md="6" class="py-0">
                  <v-text-field label="Username" required ></v-text-field>
                </v-col>
                <v-col cols="12" md="6" class="py-0">
                  <v-text-field label="Mobile" required ></v-text-field>
                </v-col>
              </v-row>
              <v-btn class="my-2 ms-4 cred">UPDATE</v-btn>
            </v-form>
          </v-card>
  </div>
</template>
<script>
export default {
  name: 'UserProfile',
  components: {},
  metaInfo: {title: 'Bizlx User Profile'},
  data() {
    return {
      activeFilePickerId: null,
      showDeleteAvatar: false,
      form1: {
        avatar_media_url: 'https://bizlx.itechvision.in/images/hot-deal-home-2.jpg',
        avatar_media_changed: false,
      },
    }
  },
  methods:{
    pickFile (id) {
      this.activeFilePickerId = id
      document.getElementById(id).click();
    },
    onFilePicked (e) {
      const files = e.target.files
      if(files[0] !== undefined) {
        if(files[0].name.lastIndexOf('.') <= 0) {
          return
        }
        const fr = new FileReader ()
        fr.readAsDataURL(files[0])
        fr.addEventListener('load', () => {
          this.form1[this.activeFilePickerId + '_media_url'] = fr.result
          this.form1[this.activeFilePickerId + '_media_file'] = files[0]
          this.form1[this.activeFilePickerId + '_media_changed'] = true
          this.showDeleteAvatar = true
        })
      } else {
        this.form1[this.activeFilePickerId + '_media_file'] = ''
        this.form1[this.activeFilePickerId + '_media_url'] = ''
        this.form1[this.activeFilePickerId + '_media_changed'] = true
      }
    }
  }
}
</script>
